import { Component, OnInit } from '@angular/core';
import { environment, eMenuAdmin } from 'src/environments/environment';
import { MainAdminComponent } from '../main-nav.component';

@Component({
  selector: 'app-quan-ly-bieu-mau',
  templateUrl: './quan-ly-bieu-mau.component.html',
  styleUrls: ['./quan-ly-bieu-mau.component.scss']
})
export class QuanLyBieuMauComponent implements OnInit {

  constructor(
    private __mainComponent: MainAdminComponent
  ) {
    localStorage.setItem(
      environment.key_storage.MENU.ADMIN,
      eMenuAdmin[eMenuAdmin.QUY_DINH_NOI_DUNG_BIEU_MAU]
    );
    this.__mainComponent.__localStorage = localStorage.getItem(
      environment.key_storage.MENU.ADMIN
    );
  }

  ngOnInit(): void {
  }

}
